.poster_card_3_pagePart{font-size:1rem;display:flex;justify-content:center;align-items:center;position:relative;z-index:0;border-radius:8px;padding:60px 10px;background-color:rgba(0,0,0,0);overflow:hidden;width:100%}@media screen and (width <= 500px){.poster_card_3_pagePart{padding:10px;border-radius:4px;gap:10px}}.poster_card_3_pagePart::before{position:absolute;z-index:-1;content:"";bottom:0%;right:calc(0% - 6px);width:600px;height:200px}.poster_card_3_pagePart .info_cont{display:flex;flex-direction:column;align-items:center;gap:20px}@media screen and (width <= 322px){.poster_card_3_pagePart .info_cont{gap:10px}}.poster_card_3_pagePart .info_cont .heading{font-size:1.5em;color:var(--third-text-color, white)}@media screen and (width <= 500px){.poster_card_3_pagePart .info_cont .heading{font-size:2em}}@media screen and (width <= 322px){.poster_card_3_pagePart .info_cont .heading{font-size:1.5em}}.poster_card_3_pagePart .info_cont .text{font-size:1em;color:var(--fourth-text-color, #f5f5f5)}.poster_card_3_pagePart .info_cont__button_cont{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:20px;margin-top:20px}.poster_card_3_pagePart .info_cont__button_cont__button{padding:10px 40px;border-radius:99999px;background-color:var(--first-background-color, white);color:var(--primary-color, teal)}@media screen and (width <= 322px){.poster_card_3_pagePart .info_cont__button_cont__button{padding:10px 30px}}.poster_card_3_pagePart .image{position:absolute;z-index:-1;top:0;left:0;width:100%;height:100%}@media screen and (width <= 800px){.poster_card_3_pagePart .image{min-width:160px}}@media screen and (width <= 500px){.poster_card_3_pagePart .image{min-width:140px}}.poster_card_3_pagePart .image img{object-position:top}